@import "/src/styles/helpers.scss";

.breadcrumbs {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 12px;

  @media #{media()} {
    margin-top: 24px;
  }
}

.item {
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;

  &:not(a) {
    opacity: 0.5;
  }
}

.ttl {
  @include h1;
  margin-top: 8px;

  @media #{media()} {
    margin-top: 24px;
  }
}
